import { Component, Input } from '@angular/core';

@Component({
    selector: 'wiki-show-comp',
    templateUrl: './wikipedia-comp.component.html',
    styleUrls:[
        './wikipedia-comp.component.css'
    ]
})

export class WikiShowComponent {
    @Input() name: String;
    @Input() intro: String;
    @Input() link: String;
    constructor() { }

}


