import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MaintRoutingModule } from './main-routing.module';

import { MainComponent } from './main.component';
import { EntryDoorComponent } from '../entry-door/entry-door.component';
import { CommonModule } from '@angular/common';
import { BasicProjectComponent } from './detail/basic/basic.component';
import { MasterMainComponent } from './master/master-main.component';
import { IntermerdiateComponent } from './detail/intermerdiate/intermerdiate.component';
import { AdvancedProjectComponent } from './detail/advanced/advancedProject.component';
import { BasicTributePageComponent } from '../basic-tribute-page/basic-tribute-page.component';

@NgModule({
    imports: [
        CommonModule,
        MaintRoutingModule
    ],
    exports: [
        RouterModule
    ],
    declarations: [
        MainComponent,
        MasterMainComponent,
        BasicProjectComponent,
        IntermerdiateComponent,
        AdvancedProjectComponent,
        EntryDoorComponent,
        BasicTributePageComponent
    ],
    providers: [
                
    ],
})
export class MainModule { }



