import { Component, OnInit } from '@angular/core';

declare var jQuery: any;

@Component({
  selector: 'app-ui-page',
  templateUrl: './ui-page.component.html',
  styleUrls: ['./ui-page.component.css']
})
export class UiPageComponent implements OnInit {
  constructor(
  ) { }

  ngOnInit() {
    jQuery('a.a_header').click(function () {
      return false;
    });
    //Add class active when scroll
    let scroll_class = jQuery('.a_header');
    jQuery(document).scroll(function () {
      let scroll_Top = jQuery(document).scrollTop();
      for (let i = 0; i < 3; i++) {
        let id = scroll_class[i].attributes[2].value;
        let h1 = jQuery(id).offset().top - 100;
        let h2 = h1 + jQuery(id).height();
        if (scroll_Top >= h1 && scroll_Top <= h2) {
          jQuery('.a_header').removeClass('active');
          let ID = id + '_a';
          jQuery(ID).addClass('active');
          break;
        }
      }
    })
    let hr_xin = `
      <div class='col-md-12'>
       <div class='col-md-3' style='margin-top:18px;height:5px;background:#757575;margin-left:21%'>
       </div>
       <div class='col-md-3' style='margin-top:18px;height:5px;background:#757575'>
       </div>
      </div>
    `
    jQuery('.hr_xin').html(hr_xin)
  }
  changeScroll(Id) {
    jQuery('.a_header').removeClass('active');
    jQuery('#' + Id + '_a').addClass('active');
    console.log(jQuery('#' + Id).offset().top);
    window.scrollTo(0, jQuery('#' + Id).offset().top);
  }
}



