import { Component, OnInit } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Component({
  selector: 'app-intermediate-wiki',
  templateUrl: './intermediate-wiki.component.html',
  styleUrls: ['./intermediate-wiki.component.css']
})
export class IntermediateWikiComponent implements OnInit {

  search_text = '';
  searchList = [];
  isLoading = false;
  failSearch = false;
  constructor(
    private http: Http
  ) { }

  ngOnInit() {
  }
  search() {
    this.isLoading = true;
    this.searchList = [];
    let controller = this;
    setTimeout(function () {
      controller.http.get('https://en.wikipedia.org/w/api.php?action=opensearch&format=json&origin=*&search=' + controller.search_text)
        .map(res => res.json())
        .subscribe(
        resJson => {
          controller.searchList = resJson;
        },
        error => {
          console.log(error);
          controller.failSearch = true;
        })
      controller.isLoading = false;
    }, 500);
    controller.failSearch = false;
  }
}



