import { Component, OnInit } from '@angular/core';
import { EntryDoorComponent } from '../../../entry-door/entry-door.component';
import { Router } from '@angular/router';

@Component({
    selector: 'intermerdiate-comp',
    templateUrl: './intermerdiate.component.html'
})

export class IntermerdiateComponent implements OnInit {
    constructor(
        private router: Router
    ) { }

    ngOnInit() { }
    changeRoute(route) {
        this.router.navigateByUrl('/intermediate/' + route)
    }
}


