import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router'
import { AppComponent } from './app.component';
import { MainComponent } from './main/main.component';
import { MainModule } from './main/main.module';
import { BasicTributePageComponent } from './basic-tribute-page/basic-tribute-page.component';
import { ErrorPageComponent } from './error-page/error-page.component';
import { UiPageComponent } from './basic-ui-page/ui-page.component';
import { IntermediateWikiComponent } from './intermediate-wiki/intermediate-wiki.component';
import { IntermediateTwitchTvComponent } from './intermediate-twitch-tv/intermediate-twitch-tv.component';
import { AdvancedCalComponent } from './advanced-cal/advanced-cal.component';
import { AdvancedWatchlockComponent } from './advanced-watchlock/advanced-watchlock.component';
import { AdvancedTicTacToeComponent } from './advanced-tic-tac-toe/advanced-tic-tac-toe.component';
import { SimmonGameComponent } from './simmon-game/simmon-game.component';

const routerConfig: Routes = [
    { path: '', component: MainComponent },
    { path: 'basic/tribute-page', component: BasicTributePageComponent },
    { path: 'basic/ui-page', component: UiPageComponent },
    { path: 'intermediate/wikipedia-page', component: IntermediateWikiComponent },
    { path: 'intermediate/twich-tv-page', component: IntermediateTwitchTvComponent },
    { path: 'advanced/calculator', component: AdvancedCalComponent},
    { path: 'advanced/watch-lock', component: AdvancedWatchlockComponent},
    { path: 'advanced/tic-tac-toe', component: AdvancedTicTacToeComponent}, 
    { path: 'advanced/simon-game', component: SimmonGameComponent},
    { path: '**', component: ErrorPageComponent }
]

@NgModule({
    imports: [
        RouterModule.forRoot(routerConfig),
        MainModule
    ],
    exports: [RouterModule],
    declarations: [

    ],
    providers: [],
})
export class AppRoutingModule { }



