import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MasterMainComponent } from './master/master-main.component';
import { BasicProjectComponent } from './detail/basic/basic.component';
import { MainComponent } from './main.component';
import { ErrorPageComponent } from '../error-page/error-page.component';
import { IntermerdiateComponent } from './detail/intermerdiate/intermerdiate.component';
import { AdvancedProjectComponent } from './detail/advanced/advancedProject.component';

const routes: Routes = [
    {
        path: '',
        component: MainComponent,
        children: [
            { path: '', component: MasterMainComponent },
            { path: 'basic', component: BasicProjectComponent },
            { path: 'intermediate', component: IntermerdiateComponent },
            { path: 'advanced', component: AdvancedProjectComponent}
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    declarations: [
    ],
    exports: [RouterModule],
})
export class MaintRoutingModule { }


