import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Component({
  selector: 'app-intermediate-twitch-tv',
  templateUrl: './intermediate-twitch-tv.component.html',
  styleUrls: ['./intermediate-twitch-tv.component.css']
})
export class IntermediateTwitchTvComponent implements OnInit {
  datas = [];
  currentDatas = [];
  isLoading: boolean;
  addresses = [];
  indexRole = 0;
  defaultImg = "../../assets/Image/default-img-twich.jpg";
  private htmlShow = "";
  constructor(
    private http: Http
  ) {
  }

  ngOnInit() {
    this.isLoading = true;
    this.addresses = ["ESL_SC2", "OgamingSC2", "cretetion", "freecodecamp", "storbeck", "habathcx", "RobotCaleb", "noobs2ninjas"];
    for (var address of this.addresses) {
      this.getData(address);
    }
    this.changeDataShow(this.indexRole);
  }
  getData(address) {
    this.http.get(this.channelAPI(address))
      .map(res => res.json())
      .subscribe(
      resJson => {
        this.addData(resJson);
      },
      error => {
        console.log(error)
      }
      )
  }
  addData(data) {
    this.datas.push(data);
  }
  changeDataShow(index) {
    this.isLoading = true;
    this.currentDatas = [];
    let controller = this;
    setTimeout(function () {
      for (var data of controller.datas) {
        if (index == 0)
          controller.currentDatas.push(data);
        else if (index == 1 && data.partner)
          controller.currentDatas.push(data);
        else if (index == 2 && !data.partner)
          controller.currentDatas.push(data);
      }
      controller.isLoading = false;
    }, 2000)
  }

  channelAPI(name) {
    return 'https://wind-bow.glitch.me/twitch-api/channels/' + name;
  }
}



