import { Component, OnInit, AfterViewInit } from '@angular/core';

declare var jQuery: any
@Component({
  selector: 'app-advanced-cal',
  templateUrl: './advanced-cal.component.html',
  styleUrls: ['./advanced-cal.component.css']
})
export class AdvancedCalComponent implements AfterViewInit {
  endNumber = '';
  afterEqual = false;

  constructor() {
  }

  ngAfterViewInit() {
    let controller = this;
    jQuery('.button_cal').click(function () {
      if (controller.afterEqual) {
        jQuery('#process').html('0');
        controller.afterEqual = false;
      }
      let HTML = (jQuery('#process').html() == '0') ? '' : jQuery('#process').html();
      let process = '';
      let val = jQuery(this).val();
      switch (val) {
        case '+':
        case '-':
        case 'x':
        case '÷':
          if (controller.checkDigitalEnd(HTML)) {
            if (HTML)
              HTML += val;
            else
              HTML = jQuery('#result').text() + val;
            controller.endNumber = '';
          }
          break;
        case '.':
          if (controller.checkDigitalEnd(HTML) && controller.checkDot(controller.endNumber)) {
            HTML += val;
            controller.endNumber += '.';
          }
          break;
        case 'AC':
        case 'CE':
          HTML = '0';
          jQuery('#result').html('0');
          controller.endNumber = '';
          break;
        case '=':
          process = controller.changePtoR(HTML);
          controller.endNumber = '';
          controller.afterEqual = true;
          break;
        default:
          controller.endNumber += val;
          HTML += val;
          break;
      }
      jQuery('#process').html(controller.limitChar(HTML));
      if (controller.afterEqual) jQuery('#result').html(eval(process));
    });
  }
  limitChar(char) {
    let arr = char.split('');
    let n = arr.length;
    if (n < 12) return char;
    else return '...' + char.substring(n - 10, n);
  }
  checkDigitalEnd(str) {
    let s = str.substring(str.length - 1);
    return Number.isInteger(+s);
  }
  changePtoR(str) {
    let Arr = str.split('');
    for (let i = 0; i < Arr.length; i++) {
      if (Arr[i] == 'x') Arr[i] = '*';
      if (Arr[i] == '÷') Arr[i] = '/';
    }
    return Arr.join('');
  }
  checkDot(str) {
    let Arr = str.split('');
    let check = 0;
    Arr.forEach(function (arr) {
      if (arr == '.') check++;
    });
    return (check > 0) ? false : true;
  }
}



