import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { EntryDoorComponent } from '../../../entry-door/entry-door.component';

@Component({
    selector: 'advanced-comp',
    templateUrl: './advancedProject.component.html'
})

export class AdvancedProjectComponent implements OnInit {
    constructor(
        private router: Router
    ) { }

    ngOnInit() { }
    changeRoute(route) {
        this.router.navigateByUrl('/advanced/' + route);
    }
}


