import { Component, OnInit } from '@angular/core';

declare var jQuery: any;
@Component({
  selector: 'app-advanced-watchlock',
  templateUrl: './advanced-watchlock.component.html',
  styleUrls: ['./advanced-watchlock.component.css']
})
export class AdvancedWatchlockComponent implements OnInit {

  break_length: number;
  session_length: number;
  interval: any;
  loading: boolean;
  constructor() { }

  ngOnInit() {
    this.break_length = 5;
    this.session_length = 1;
    this.interval;
    this.loading = false;
    let controller = this;
    jQuery('#break_length i').click(function () {
      if (this.classList[1] == 'fa-plus') {
        controller.break_length++;
      }
      else {
        if (controller.break_length != 0) controller.break_length--;
      };
      jQuery('#break_length p').html('' + controller.break_length);
    });
    jQuery('#session_length i').click(function () {
      if (this.classList[1] == 'fa-plus') {
        controller.session_length++;
      }
      else {
        if (controller.session_length != 1) controller.session_length--;
      }
      jQuery('#session_length p').html('' + controller.session_length);
      if (!controller.loading)
        jQuery('#clock p').html(controller.session_length + ':' + jQuery('#clock p').text().split(':')[1]);
    });
    jQuery('#clock').click(function () {
      controller.loading = !controller.loading;
      if (controller.loading) {
        let time_begin = jQuery('#clock p').text();
        controller.interval = setInterval(function () {
          let arr = jQuery('#clock p').text().split(':');
          let minute = +arr[0];
          let second = +arr[1];
          minute = (second == 0) ? minute - 1 : minute;
          second = (second == 0) ? 59 : second - 1;
          let time_now = controller.format_show(minute) + ':' + controller.format_show(second);
          jQuery('#clock p').html(time_now);
          let percent = controller.Percent(time_now, time_begin);
          jQuery('#clock').css('background', 'linear-gradient(to bottom, #333333 ' + percent + '%, #9CCC65 ' + (100 - percent) + '%)');
          if (minute == 0 && second == 0) clearInterval(controller.interval);
        }, 1000);
      }
      else {
        clearInterval(controller.interval);
      }
    })
  }

  format_show(number) {
    return (Math.floor(number / 10) == 0) ? ('0' + number) : ('' + number);
  }

  Percent(time_now, time_begin) {
    let arr1 = (time_now + '').split(':');
    let arr2 = (time_begin + '').split(':');
    return 100 * (+arr1[0] * 60 + (+arr1[1])) / (+arr2[0] * 60 + (+arr2[1]));
  }

}



