import { Component, OnInit } from '@angular/core';

declare var jQuery: any;
@Component({
  selector: 'app-simmon-game',
  templateUrl: './simmon-game.component.html',
  styleUrls: ['./simmon-game.component.css']
})
export class SimmonGameComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    let audio_sound = {
      audio0: "https://s3.amazonaws.com/freecodecamp/simonSound1.mp3",
      audio1: "https://s3.amazonaws.com/freecodecamp/simonSound2.mp3",
      audio2: "https://s3.amazonaws.com/freecodecamp/simonSound3.mp3",
      audio3: "https://s3.amazonaws.com/freecodecamp/simonSound4.mp3",
    }
    let wrong_audio = "http://themushroomkingdom.net/sounds/wav/smb/smb_mariodie.wav";
    let win_audio = "http://themushroomkingdom.net/sounds/wav/smb/smb_stage_clear.wav";
    let switch_position = 'left',
      playing = false,
      step = 1,
      stepNow = 0,
      playNow = false,
      randomPlay = [],
      numberWin = 100;
    jQuery('#switch_1').click(function () {
      playing = !playing;
      switch_position = (switch_position == 'left') ? 'right' : 'left';
      jQuery('#control').css('float', switch_position);
      if (switch_position != 'left')
        jQuery('#screen').removeClass('ledOff');
      else {
        jQuery('#screen').addClass('ledOff');
        resetGame();
      }
    });
    jQuery('#strict button').click(function () {
      if (playing) {
        if (jQuery('#light_strict')[0].classList.length == 0)
          jQuery('#light_strict').addClass('light');
        else jQuery('#light_strict').removeClass('light');
      }
    });
    jQuery('.buttn').click(function () {
      if (playNow) {
        let num = +jQuery(this)[0].id;
        if (num == randomPlay[stepNow]) {
          playAudio(num);
          stepNow++;
        }
        else {
          jQuery('#' + num).addClass('light');
          setTimeout(function () {
            jQuery('#' + num).removeClass('light');
          }, 200);
          // document.getElementById('wrong-sound').play();
          let audio = new Audio();
          audio.src = wrong_audio
          audio.play();
          setTimeout(nextStep(), 10000);
        }
        if (stepNow == step) {
          if (stepNow == numberWin) {
            jQuery('#simon_game').hide();
            jQuery('#win').fadeIn(200);
            let audio = new Audio();
            audio.src = win_audio
            audio.play();
          }
          step++;
          nextStep();
        }
      }
    })
    function playAudio(num) {
      jQuery('#' + num).addClass('light');
      setTimeout(function () {
        jQuery('#' + num).removeClass('light');
      }, 200);
      let audio = new Audio();
      audio.src = audio_sound['audio' + num]
      audio.play();
    }
    jQuery('#start button').click(function () {
      if (playing) {
        nextStep();
        jQuery('#screen').val(formatStep(step));
      }
    });
    function resetGame() {
      step = 1;
      jQuery('#light_strict').removeClass('light');
      jQuery('#screen').addClass('ledOff');
      jQuery('#screen').val('--');
    }
    function formatStep(step) {
      if (+step < 10) return '0' + step;
      else return step + '';
    }
    function nextStep() {
      stepNow = 0;
      jQuery('#screen').val(formatStep(step));
      randomPlay = [];
      let i = 0;
      for (i = 0; i < step; i++) {
        let num = Math.floor(4 * Math.random());
        randomPlay.push(num);
        setTimeout(function () {
          playAudio(num);
        }, 1000 * (i + 1));
      }
      playNow = true;
    }
  }

}



